import React, { Component } from 'react';

import './App.css';
import Articles from '../components/Articles/Articles.js';
import Layout from '../components/Layout/Layout.js';
const week3prev = require('../data/articles/week3_prev');
const week5prev = require('../data/articles/week5_prev');

class App extends Component {

  state = {
    week3prev: week3prev.week3prev,
    week5prev: week5prev.week5prev
  };

  togglerArticlesHandler = (event) => {
      const doesShow = this.state.showArticles;
      this.setState({showArticles: !doesShow});
  }

  render() {

    return (
      <div className="App">
        {/*<Backdrop />*/}
        <Layout> 
          <div>
            <Articles
            // articles={this.state.week3prev}
             articles={this.state.week5prev}
             waivers={this.state.waivers} />        
          </div>
        </Layout>

        {/*<button onClick={this.togglerArticlesHandler}>Show older articles...</button>
        {this.state.showArticles ? <Articles articles={this.state.articles} /> : null}*/}
      </div>
    );
  }
}

export default App;



