import React from 'react';
import Matchup from './Matchup/Matchup';

const matchups = (props) => {
    return (
        <div>
            {props.matchups.map((m, index) => {
                return <Matchup
                    team1={m.team1}
                    rank1={m.rank1}
                    wins1={m.wins1}
                    losses1={m.losses1}
                    team2={m.team2}
                    wins2={m.wins2}
                    losses2={m.losses2}
                    rank2={m.rank2}
                    gotw={m.gotw}
                    synop1={m.synop1}
                    synop2={m.synop2} 
                    qb={m.QB}
                    wr={m.WR}
                    rb={m.RB}
                    te={m.TE}
                    k_def={m.K_Def}
                    winner={m.Winner}
                    />
                }
            )}
        </div>
    );
}

export default matchups;


