import React from 'react';
import classes from './Article.css';
import Matchups from './Matchups/Matchups';
import Waivers from './Waivers/Waivers'

const article = (props) => {
    return (
        <div className={classes.Article}>
            <h2>Week {props.week} Matchups</h2>
            <br />
            <p>{props.weeklyRamblings}</p>
            <br />
            <Matchups matchups={props.matchups}/>
            <br />
            <br />
            <h2>Waiver Wire</h2>
            <p>{props.waiverRamblings}</p>
            <Waivers waivers={props.waivers} />
        </div>
    );
}

export default article;



