import React from 'react';

import siteLogo from  '../../assets/images/gmaDab.gif';
import classes from './Logo.css';

const logo = (props) => (
    <div className={classes.Logo}>
        <img src={siteLogo} alt="CD Logo"/>
    </div>
);

export default logo; 


