import React from 'react';
import Waiver from './Waiver/Waiver.js';

const waivers = (props) => {
    return (
        <div>
            {props.waivers.map((w, index) => {
                return <Waiver 
                         player={w.player}
                         cost={w.cost}
                         owner={w.owner}
                         snippet={w.snippet} /> 
                }
            )}
        </div>
    );
}

export default waivers;


