import React from 'react';

import classes from './Waiver.css'

const waiver = (props) => {
    return (
        <div className={classes.Waiver}>
            <h4>{props.player  + " $" + props.cost + "  (" + props.owner + ")"}</h4>
            <p>{props.snippet}</p>
            <br />
        </div>
    );
}

export default waiver;


