module.exports={week3prev:[{id:1,week:3,type:"Preview",showArticle:true,weeklyRamblings:"Another season, some more ramblings from a highly un-qualifed sports journalist (all errors / confusing sentences are not the fault of the writer). If you enjoy what you read here today, please reach out with lavish gifts and/or ideas for fresh content that would be fun to see this season. "+"Also, I ask (perhaps foolishly) that if anyone would like to help write recaps or previews or whatever your heart desires for any and all weeks, simply send your content into the editor :) ",matchups:[{team1:"Rubb a Dubb Chubbski",rank1:4,wins1:1,losses1:1,team2:"D.T.P. Futbol",rank2:5,wins2:1,losses2:1,gotw:false,synop1:"After recieving a beat down in week 1, the Chubbski's bounced back in week 2 and left the Brown Cousins devastated. Looking now at week 3, the Polish based squad"+" had three Cleveland Browns on TNF (three!?!?), Jarvis and the Cleveland D put up respectable numbers, while the Duke of Johnson didn't do anything fancy. Yo, how about "+"Michael Thomas's start to the season!?! Dude is from another planet right now. To pull off the win vs DTP, Gurley will need to do Gurley things and Trubisky"+" will need to take advantage of possibly his easiest matchup of the season with Arizona.",synop2:"DTP started off hot in week 1 throwing up an always pleasing 200+ point game, then stumbled against D.o.D. and their FitzMagic (even though he was benched)."+" Thus far, DTP's feel good story lies with the freaky Mr. Tyreeky and his good JuJu WR sidekick. At running back, Corey Clement "+"looks set for a legitimate workload and it's only a matter of time until \"Get your Kerry on\" Johnson breaks out. However this matchup shakes out, it'll be big for either team.",QB:"DTP",WR:"Chubbski",RB:"DTP",TE:"DTP",K_Def:"DTP",Winner:"DTP"},{team1:"Psycho D",rank1:2,wins1:2,losses1:0,team2:"Brown Cousin",rank2:9,wins2:0,losses2:2,gotw:false,synop1:"The Psychos are off to a fast start in 2018, they're averaging nearly 200 pts/game and find themselves sitting alone atop the Premier League (PL)."+" Despite both Devonta and Dalvin being ruled out this week, the RB corps is still looking solid and is projected for 50+ points. With Leveon potentially sitting out the entire season "+"drafting James Conner is looking like a real coup. There is a lack of QB depth on the squad, it'll be interesting to see how Josh Allen pans out. With a great 1-2 punch at TE, this team is currently hard to beat.",synop2:"Despite being the 3rd highest scoring team in the league through two weeks, the B-Cuzzins find themselves desperately need a win to stay in contention in the Champions League (CL). "+"The Cuzzins are swimming in solid QB's, Bortles appears finally viable, Kirk is looking sharp in Minnesota and it is likely only a matter of time before Deshaun Watson starts sniffing at his elite 2017 form. "+"Pillar and superstud, Angry Antonio, finds himself in a curious early season situation, but no worries as of yet. With no glaring weaknesses "+"the Tri-City based owner should be confident in his teams potential despite the winless start. WTF is he starting Dallas's D for though?",QB:"Cuzzins",WR:"Cuzzins",RB:"Cuzzins",TE:"Psychos",K_Def:"Psychos",Winner:"Cuzzins"},{team1:"Death of a Dynasty",rank1:1,wins1:2,losses1:0,team2:"Korean WingDings",rank2:3,wins2:1,losses2:1,gotw:true,synop1:"The 2017 runner-ups are picking up where the left off last season averaging a delectable 207 pts/game, meanwhile, FITZMAGIC (50 pts/game) hasn't even been in the starting lineup! "+"With Andrew Luck back in the fold and playing reasonably well, the quarterback position is likely considered average around the league, but effective. It'll be fun to see how Fitzy plays the next few weeks and if Jameis is put back into the fold. "+"For a couple years now, the key ingredient to D.o.D's success is their incredible stable of WR's, and thus far in 2018 there's no sign of decline in that department. "+"The early season success isn't all due to Deandre / Keenan / A.J. & Mike, their was a clear focus in the draft to select versatile, point-producing running backs and so far it's working out nicely. "+"D.o.D. added to that group by recently splashing out $29 precious FAAB on the Gio of Bernard, we'll see how that goes. Jared Cook may have had the best game of his career x10 in Week 1, but that is completely "+"un-sustainable, therefore, the TE position is a bit of a work in progress considering Cook is paired with the oft-injured Tyler Eifert. ",synop2:"After a tough loss to the Marauders, it doesn't get any easier for the WingDings in week 3, they now face a fellow Champions League contender with a well-balanced team that brings death to dynasties. "+"Quarterback hasn't been a weak spote for the Mill Creek based squad in quite some time, Drew & Cam continue to hold down the fort, but there could be a concern with the lack of depth behind them. "+"The reciever position has been productive thus far, the great white Adam Thielen (a true Robotron type of player) and Davante Adams have been nothing but consistent dating back to last season. Kenny Golladay is trying "+"to make a name for himself as well. It's no secret that the WingDing's biggest strength in recent seasons has been the RB position, but at this point in 2018 there is some turmoil in Pittsburgh and poor play in Arizona holding the group back from their usual elite status. "+"Perhaps the young bucking Broncos can help solidfy the position and make it an obvious strength again. After trading for and keeping Trey Burton before the season started, there was great expectation for the former Eagle, "+"but thanks to the Trubisky factor there hasn't been much return on that investment as of yet. ",QB:"WingDings",WR:"Death",RB:"Kamara",TE:"WingDings",K_Def:"WingDings",Winner:"WingDings"},{team1:"Mtn. View Marauders",rank1:6,wins1:1,losses1:1,team2:"El Presidente",rank2:9,wins2:0,losses2:2,gotw:false,synop1:"After a disappointing game to open the season, the Marauders avoided the dreaded 0-2 start thanks to Russell flexin his muscle late Monday night. Riots in Mtn. View increased by 39% in the days that followed. "+"Despite the excellent victory against the 2016 champs, the Marauders squad could use a shot in the arm. A-Rodge is hurt and flirting with an extended absence, Russell is in struggle city, "+"and the RB corps is temporarily being propped up by Tevin Coleman's fill-in duty. The enigmatic Josh Gordon landing in New England could be just what the doctor ordered, "+"but there's really no evidence JG (the football version) will ever get his proverbial sh*t together.",synop2:"With a paltry 151 point average thus far, the start of the season has been one to forget for Mr. Roig and his Presidentes. It appears the causes are simply related "+"to unfortunate injuries and early season inconsistency from the squad. The good news is that the hero of Philadelphia, Wentzy boy himself, is set to return to the starting lineup. He'll join forces with fellow 3rd year player, Jared Goff, to inflict heavy point based damage on their foes. "+"Though, in order to get off the schnide this week against the Marauders, El Presidente's core dogs need to eat. Looking at you Julio, Leonard, Derrick & Amari. ",QB:"Mtn. View",WR:"Prez",RB:"Mtn. View",TE:"Mtn. View",K_Def:"Prez",Winner:"Mtn. View"},{team1:"Take Mahomes Tonight",rank1:7,wins1:1,losses1:1,team2:"Robotron",rank2:8,wins2:1,losses2:1,gotw:false,synop1:"The 2019 season has started with promise in Mahomes land, as its filled with talented young bucks trying to come into their own and savvy vets up to their old tricks. It's apparent last season's investment in Mahomes "+"has finally given this team a much needed clear-cut #1 QB, now can they find that consistent #2? The RB group isn't super deep, but there is obvious talent that may come together nicely sooner rather than later. "+"While the squad of fantasy players may have a bright future, what is with the team name!?! There's no clever nicknames to pull from it (if you find one let me know) and it just doesn't have mojo, maybe the Dilly Dogg is getting too much sign down in AZ. ",synop2:"2018's last place finisher is sitting at .500 and has an eye on crashing the Mahomes party bus here in week 3. Anyone else notice the #1 pick in 2018 and the lord and savior of the Robo's caught "+"14 f'n passes last week? I'd say Saquon is off to a solid start to his fantasy career. The cast of hooligans on this squad is looking much improved over last season and with room to grow. Baker's performance on TNF was outstanding and promising for the future. "+"As for this week though, Tyrod's TNF performance did not help matters, which is unfortunate because the Crow dogg put up a solid game. Despite the Robo's having the advantage at most positions this week, it's unclear they'll be able to make-up for Tyrod's TNF tragedy. ",QB:"Mahomes",WR:"Robo",RB:"Robo",TE:"Robo",K_Def:"Robo",Winner:"Mahomes"}],waiverRamblings:"",waivers:[{player:"Giovani Bernard",cost:"29",owner:"D.o.D.",snippet:"Miiight have been a bit much to pay for ole Gio B (appears to be a pure cock-block), but who's to question the #1 ranked team in the land? [UPDATE]: Gio is now in the starting lineup as of Sunday morning."},{player:"Duke Johnson",cost:"9",owner:"Polska!",snippet:"The Polish got their Duke back, maybe with Bake at the helm he'll produce?"},{player:"Geronimo Allison",cost:"7",owner:"Browny Cuzz",snippet:"Geronimooooooo. Should there be a Randall Cobb or Davante Adams injury... "},{player:"Chris Ivory",cost:"6",owner:"Mtn. View",snippet:"A textbook handcuff move."},{player:"Ito Smith",cost:"3",owner:"D.T.P.",snippet:"A baller in college and a nice showing in week 2 left D.T.P.'s owner intrigued with the possibliites."},{player:"Jesse James",cost:"2",owner:"Mahomes",snippet:"After a hot 2 weeks for Jesse, the Mahomes group felt a $2 holla was worth the while."},{player:"Joshua Perkins",cost:"1",owner:"D.T.P.",snippet:"A deep and desperate attempt at finding a fill-in TE2 for week 3."},{player:"Mark Walton",cost:"1",owner:"D.T.P.",snippet:"A handcuff to the handcuff. "},{player:"Tyler Boyd",cost:"0",owner:"Psycho's",snippet:"After a great week 2, this gentle sir is certainly worth a stab."},{player:"Latavius Murray",cost:"0",owner:"Psycho's",snippet:"Wow, this handcuff move is panning out real quick. Somewhat surprising he wasn't already owned."}]}]};

