module.exports={week5prev:[{id:1,week:5,type:"Preview",showArticle:true,weeklyRamblings:"Week 5 already, damn time goes by fast. Once again, I am always accepting offers for content or content ideas. I have plans to put power rankings and standings and history and all sorts of goodies on this site. For now though, it's a preview show.",matchups:[{team1:"Brown Cousin",rank1:8,wins1:1,losses1:3,team2:"Death of a Dynasty",rank2:3,wins2:3,losses2:1,gotw:true,synop1:"The Brown Cousins sit joint top scorers in the league but are only 1-3! Fortunes changed in week 4 though, the B-Cuzzins finally got off the schnide against DTP, disposing them with ease. \n                    For comparison, Mr. Pfleeger's squad has 817 points against in their 4 games, while the 3-1 Psychos only have 714 points against thus far. \n                    The lineup decision at QB this week could be a game deciding one. You get the feeling that Watson is a must start each week, but QB2 is between Cousins vs Philly & Bortles vs KC. \n                    At reciever it appears Cooper Kupp is going to slot in as a year over year keeper with Antonio Brown, meanwhile, at RB there's rumblings that rookie Nyheim Hines could be forcing Lamar Miller out of the starting lineup in the near future.\n                    Finally, the tight end position is riddled with injuries, much like the rest of the league. ",synop2:"D.o.D. has been impressively consistent so far this season, last week they denied the El Prez their first win in an epic 230 to 224 point matchup. \n                    This week they enter week 5 with a chance to bury a division rival in the standings. A big part of the team's success so far has been Andrew Luck's resurgence. \n                    Lucky boy got DoD's off to a nice start on TNF, now it's Dak's turn to step up and do some damage as well. Chris McCaffrey is coming off a bye and one of his best peformances so far as pro, D.o.D. has found themeslves an RB1. \n                    Quincy Enunwa has some big shoes to fill at WR while Mike Evans sits out on a bye. Eifert went down with a season ender (yet again, poor fella), but at least Jared Cook isn't showing signs of slowing down.",QB:"Death (maybe B-Cuz if Bortles plays)",WR:"Push",RB:"Push",TE:"Death",K_Def:"B-Cuz",Winner:"Death (friggin close one)"},{team1:"El Presidente",rank1:10,wins1:0,losses1:4,team2:"Psycho D",rank2:4,wins2:3,losses2:1,gotw:false,synop1:"If you disregard the week 2 beatdown they suffered, El Presidente has lost their other 3 games by a brutally low total of 14 points, ay caramba!. \n                    On the bright side, Jared Goff is looking like he's in a great situation for years to come and we all know how well Wentz was playing last season until he went down. \n                    Can old man Peterson keep up his energy levels?",synop2:"Tyler Boyd is clickin, the dude is lighting up the fantasy scoreboard this season and is a big part of the Psycho success in recent weeks.\n                    I don't see the squad slowing down here in week 5, the matchups at QB favor are in favor of the Psycho's and rookie Sony Michel pumped out 22 points on TNF to jump-start the scoring.\n                    If Devonta Freeman steps back into his role and does well, the Presidente's are going to have a hard time finding their first win yet again.",QB:"Psycho",WR:"Prez",RB:"Psycho",TE:"Psycho",K_Def:"Prez (Seahawks are gonna ball out, right???)",Winner:"Psycho"},{team1:"Take Mahomes Tonight",rank1:2,wins1:3,losses1:1,team2:"Mtn. View Marauders",rank2:5,wins2:2,losses2:2,gotw:false,synop1:"Things are good in Mahomes land, in fact they could probably lose the rest of their games this season and the DillyDogg would still be in the honeymoon phase with Mahomey.\n                    2017 holdovers Corey Davis and Robert Woods really stepped up last week and it looks like there will be more good things to come from them. Sidenote, Sterling Shep was stolen from DTP a couple weeks ago (drugs may have been involved).\n                    Nothing special cooking at RB or TE this week, but they shouldn't put up goose-eggs either.",synop2:"The Marauders are hanging around at .500 and the current projected point totals suggest they may be catching a division rival on the right week. It appears Russell may be hitting the bench? I can't remember the last time I've seen that.\n                    Odell has not been his WR1 self lately, but rookie stud Calvin Ridley has sure made his presence known in Atlanta, up until this point that guy looks like a steal.",QB:"Mtn. View",WR:"Mtn. View",RB:"Mtn. View",TE:"Mtn. View (holy Ebron)",K_Def:"Mahomes",Winner:"Marauding will take place"},{team1:"Rubb a Dubb Chubbski",rank1:6,wins1:2,losses1:2,team2:"Robotron",rank2:7,wins2:2,losses2:2,gotw:false,synop1:"The Polish ride into week 5 off a loss to the WingDings, even though the great white hope (also known as Trubisky) threw for 6 TD's. So far this season, the Chubbski's have only topped 166 points one time. \n                     Michael Thomas came back down to earth last week with only 4 grabs, remarkably he's caught 42 of 44 targets so far this year. Second year WR Mike Williams has been a pleasant surprise in 2018, but the squad could use some spark at RB.\n                     Only the Gurley man is projected to score more than 9 points in the starting lineup this week.",synop2:"The Robo's are certainly looking more threatening this year compared to last, last week they put up 200+ points while securing a win vs the Marauders. \n                     Even if this season doesn't end in a playoff berth there is a youthful energy surrounding this up and coming squad. Saquan is still looking the part, but he faces a tough test in Carolina this week. \n                     Bake (as he's affectionately named by his colleagues) is bringing energy at the QB position to both the Browns and the Robos. Bake's running mate Derek Carr is also starting to recapture some of his old form under coach Gruden.\n                     The RB position is looking surprisingly solid beyond Saquon and if Will Fuller balls out again this week, Adamo's team is likely to have a winning record heading into week 6.",QB:"Chubbski (Atlanta has no defense)",WR:"Robo",RB:"Robo",TE:"Robo",K_Def:"Chubbski",Winner:"Robotron for the win"},{team1:"D.T.P. Futbol",rank1:9,wins1:1,losses1:3,team2:"Korean WingDings",rank2:1,wins2:3,losses2:1,gotw:false,synop1:"Oof, after a heartbreaker in week 3, DTP took a beating in week 4 at the hands of the B-Cuzzins. Sitting at 1-3 and currently going up against the WingDings does not paint a pretty picture. \n                    TNF was a much needed shot in the arm to start the week. Tom did well and it looks as if Julian & Gronk are healthy and primed for a good season. The RB group is looking young and studly, Kareem looked dreamy last week,\n                    while Mixon and Kerryon have some nice potential. Help at TE2 is needed and DTP has been wiling to overspend for potential. ",synop2:"Hard to argue with the talent on this squad, especially when the Brees / Cam combo both go nuts in the same week. Could Mark Ingram's return help those of us who have to play this team? \n                    It'll be interesting to see how the Le'Veon situation shakes out. Does the WingDings owner wait and see how things unfold the rest of the yaer or dish the guy now for some fantasy goods? ",QB:"DTP",WR:"Push",RB:"WingDings",TE:"DTP",K_Def:"DTP",Winner:"Stafford goes bonkers and DTP pulls down the W (perhaps this is hopeful thinking)"}],waiverRamblings:"",waivers:[{player:"Tennessee D",cost:"3",owner:"Mtn. View Marauders",snippet:"Tennesse is playing well and could easily squash Joshy Boy Allen up in Buffalo"},{player:"Cameron Brate",cost:"5",owner:"Death of a Dynasty",snippet:"A potentially sweet lil pick-up, this guy can play and OJ is out a few weeks"},{player:"Nyheim Hines",cost:"17",owner:"Brown Cousin",snippet:"The Cousins narrowly outbid the Polish for Nyheim's services, he could be a player too. "},{player:"Carolina D",cost:"1",owner:"Brown Cousin",snippet:"Coming off a bye and ready to stomp on Eli"},{player:"Nick Vannett",cost:"8",owner:"Take Mahomes Tonight",snippet:"The Dizzle has gone down, now the Vizzle must step up."}],powerRank:[{rank:1,team:"Take Mahomes Tonight",change:2},{rank:2,team:"Korean WingDings",change:0},{rank:3,team:"Death of a Dynasty",change:3},{rank:4,team:"Psycho D",change:-3},{rank:5,team:"Brown Cousin",change:3},{rank:6,team:"Robotron",change:3},{rank:7,team:"Mtn. View Marauders",change:-3},{rank:8,team:"Rubb a Dubb Chubbski",change:-33},{rank:9,team:"D.T.P. Futbol",change:-2},{rank:10,team:"El Presidente",change:0}]}]};

