import React from 'react';

import classes from './Matchup.css';

const matchup = (props) => {
    return(
        <div className={classes.Matchup}>
            <h3>{props.gotw ? "MATCHUP OF THE WEEK !!!" : null}</h3>
            <h4>{"#" + props.rank1 + "  " + props.team1 + " (" + props.wins1 + "-" + props.losses1 +
                ")  vs  #" + props.rank2 + "  " + props.team2 + " (" + props.wins2 + "-" + props.losses2 + ")"}</h4>
            <p>{props.synop1}</p>
            <p>{props.synop2}</p>
            <br />
            <h5>{"QB - " + props.qb}</h5>
            <h5>{"WR - " + props.wr}</h5>
            <h5>{"RB - " + props.rb}</h5>
            <h5>{"TE - " + props.te}</h5>
            <h5>{"K/Def - " + props.k_def}</h5>
            <h5>{"Winner - " + props.winner}</h5>
            <hr />
        </div>
    );
}

export default matchup;


