import React from 'react';
import Article from './Article/Article';

const articles = (props) => {
    return(
        <div>
            <h2>{props.type}</h2>
            {props.articles.map((article, index) => {
              return <Article 
                key={article.id}
                week={article.week} 
                matchups={article.matchups}
                weeklyRamblings={article.weeklyRamblings}
                waivers={article.waivers}
                waiverRamblings={article.waiverRamblings}
                 />
            })}
        </div>
    );
}

export default articles;


